<!-- eslint-disable -->
<template>
  <div>
    <!--Header-->
    <b-container>
      <base-header class="pb-6">
        <div slot="tabs">
          <sw-type-tab :categories="categories" />
        </div>
      </base-header>
    </b-container>
    <!-- Fim Header-->

    <div class="sections">
      <!-- Content Card -->
      <template v-for="category in categories">
        <!-- Scroll Menu -->
        <scroll-Tab :options="category" v-show="tabControl == category.type" />
        <!-- Fim Scroll Menu -->

        <b-container>
          <!-- Seções de categorias de produtos -->
          <template v-for="item in category.data">
            <strong v-show="tabControl == category.type">{{ item.category_name }}</strong>
            <section :id="item.category_id" v-for="product in item.products" v-show="tabControl == category.type">
              <div class="container" :id="`product-${product.id}`">
                <div class="row ml-4 action-div" @click="toggleDetail(product.id)">
                  <div class="col-1 ml-1">
                    <img class="avatar avatar-lg" :src="`http://192.168.100.51:8000${product.path}`" alt="logo">
                  </div>
                  <div class="col-8">
                    <div class="row">
                      <div class="col-12  ml--3 mt--2">
                        <span class="product-name"> {{ product.name }}</span>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-12 col-sm-11 ml--3">
                        <p class="product-description">{{ product.description }}</p><br>
                        <p class="product-portion">{{ product.portion }}</p>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-12 mt-4 ml--3">
                        <strong v-if="!product.promo" class="product-price">{{ maskedValue(product.price) }}</strong>
                        <strong v-if="product.promo" class="product-price-promo">{{ maskedValue(product.price) }}</strong>
                        <strong v-if="product.promo" class="promo-price">{{ maskedValue(product.promo) }}</strong>
                      </div>
                    </div>
                  </div>
                </div>

                <div class="row ml--4 ml-sm-2 content-detail" :id="`content-detail-${product.id}`" v-if="featureToggle">
                  <div class="col-12 col-sm-11">
                    <div class="row mt-1 ml-sm-3">
                      <div class="col-6 col-sm-7 ml-sm--2 mr-sm-4">
                        <sw-content-spinner @change="changeSpinner" />
                      </div>

                      <div class="col-3 col-sm-3  ml--3 ml-sm-5  mt-1">
                        <span class="content-price">Total: <strong>{{ maskedValue('71.0') }}</strong></span>
                      </div>
                    </div>

                    <div class="row mt-1 ml-sm-2 ml--3">
                      <div class="col-9 col-sm-11 ml--1 ml-sm--1">
                        <input v-if="tabControl == 'food'" type="text" class="observation"
                          placeholder="Ex: sem cebola, sem sal...">
                      </div>

                      <div class="col-1 col-sm-1 ml--2">
                        <button class="btn btn-danger btn-red btn-sm btn-order" @click="order(product.id)">Pedir</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </template>
          <!-- Fim Seções de produtos -->
        </b-container>
      </template>
      <!-- Fim Content Card -->

      <!-- Anuncios -->
      <div class="market-banner" style="height: 600px;">
        <img v-if="tabControl == 'food'" height="250px" width="300px"
          src="https://www.pngarts.com/files/1/Veg-Food-PNG-Pic.png">
        <img v-else height="250px" src="https://imagensemoldes.com.br/wp-content/uploads/2020/10/Figura-Cocktail-PNG.png">
      </div>
      <!-- Fim Anuncios -->
    </div>
  </div>
</template>
<script>
import categories from './categories'

export default {
  data() {
    return {
      tabControl: 'food',
      categories: categories,
      contentControl: null,
      featureToggle: false
    }
  },
  mounted() {
    $(`.content-detail`).hide()

    this.$eventHub.$on("changed-type", (value) => {
      this.tabControl = value
    });
  },
  methods: {
    maskedValue(value) {
      return this.$maskedValue(value, { language: 'contry', currency: 'BRL' })
    },
    toggleDetail(id) {
      // Feature toggle será substituída por uma ação casl
      // Foco em concluír o cardápio
      if (!$(`#product-${id}`).parent().hasClass('expand-content') && this.featureToggle) {
        $(`.content-detail`).hide()
        $('section').removeClass('expand-content')
        $(`#product-${id}`).parent().addClass('expand-content')
        $(`#content-detail-${id}`).fadeIn(1000);
      }
    },
    order(id) {
      console.log(id);
    },
    changeSpinner(value) {
      console.log(value);
    }
  },
};
</script>

<style scoped>
.product-name {
  font-size: 12px;
  font-weight: 600;
}

.product-description {
  font-size: 10px;
  margin-top: -3px;
  margin-bottom: -24px;
  line-height: 1.3;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  width: 140%;
  text-align: left;
}

.product-portion {
  font-size: 9px;
  text-align: left;
  color: black;
  font-weight: 500;
  max-width: 70%;
  line-height: 1.07;
  margin-bottom: -29px;
}

.product-price {
  font-size: 10px;
  margin-right: 3px;
  color: black;
}

.product-price-promo {
  font-size: 10px;
  margin-right: 3px;
  color: green;
}

.promo-price {
  font-size: 10px;
  color: grey;
  text-decoration: line-through;
}

.sections {
  margin-top: 150px;
}

section {
  display: -webkit-inline-box;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #dcdbdb;
  height: 80px;
  padding-top: 8px;
  padding-left: 8px;
}

.market-banner {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-top: 15px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

.expand-content {
  height: 160px;
  transition: all 0.3s ease-in-out;
}


.btn-red {
  background-color: red;
}

.btn-order {
  margin-left: 11px;
}

.observation {
  width: 90%;
}

.content-price {
  font-size: 13px;
  color: black;
  float: right;
  margin-right: -93px;
}

.action-div {
  cursor: pointer;
}
</style>
